import React, { useState } from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  TextField,
  DialogActions,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import { signInWithGoogle, signInWithEmailAndPassword } from './firebase';

const Header = ({ user, onUserChange }) => {
  const [loginOpen, setLoginOpen] = useState(false);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const handleLoginClick = () => {
    setLoginOpen(true);
  };

  const handleClose = () => {
    setLoginOpen(false);
    setEmail('');
    setPassword('');
  };

  const handleGoogleLogin = async () => {
    try {
      await signInWithGoogle();
      // Handle successful login
      console.log('Google login successful');
    } catch (error) {
      // Handle login error
      console.error('Google login error:', error);
    }
  };

  const handleEmailLogin = async (e) => {
    e.preventDefault();
    try {
      await signInWithEmailAndPassword(email, password);
      // Handle successful login
      console.log('Email login successful');
      handleClose();
    } catch (error) {
      // Handle login error
      console.error('Email login error:', error);
    }
  };

  return (
    <>
      <AppBar position="static" elevation={0} sx={{ backgroundColor: 'white' }}>
        <Toolbar>
          <Box display="flex" alignItems="center" width="100%" py={2}>
            {/* Logo Space */}
            <Typography
              variant="h6"
              component="div"
              sx={{ flexGrow: 0, color: 'primary.main', fontWeight: 'bold', mr: 2 }}
            >
              PropertyAI
            </Typography>

            {/* Tagline */}
            {!isMobile && (
              <Typography
                variant="subtitle1"
                sx={{ flexGrow: 1, color: 'text.secondary' }}
              >
                Instant Property Development Insights
              </Typography>
            )}

            {/* Login/Signup Button */}
            {user ? (
              <Button
                color="primary"
                onClick={() => onUserChange(null)}
              >
                Logout
              </Button>
            ) : (
              <Button
                color="primary"
                variant="outlined"
                onClick={handleLoginClick}
              >
                Login / Sign Up
              </Button>
            )}
          </Box>
        </Toolbar>
      </AppBar>

      {/* Login Dialog */}
      <Dialog open={loginOpen} onClose={handleClose}>
        <DialogTitle>Login / Sign Up</DialogTitle>
        <DialogContent>
          <Box display="flex" flexDirection="column" gap={2}>
            <Button variant="contained" onClick={handleGoogleLogin}>
              Login with Google
            </Button>
            <form onSubmit={handleEmailLogin}>
              <TextField
                autoFocus
                margin="dense"
                label="Email Address"
                type="email"
                fullWidth
                variant="outlined"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
              <TextField
                margin="dense"
                label="Password"
                type="password"
                fullWidth
                variant="outlined"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
              <Button type="submit" variant="contained" fullWidth sx={{ mt: 2 }}>
                Login with Email
              </Button>
            </form>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default Header;
